
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


export default {
data () {
return {
accno: '',
weixinId: 'weixinId',
certList: [['身份证', '户口本']],
certType: ['身份证'],
certId: '',
mobile: '',
verifyCode: '',
isGetVerifyCode: false,
checked: false,
count: 0,
toastText: '',
showPositionValue: false
}
},
components: {
XHeader,
XButton,
Group,
Cell,
XInput,
PopupPicker,
Toast
},
computed: {
count_show () {
return this.count === 0 ? '重新获取' : `(${this.count}s)`
}
},
watch: {
count: function (val) {
if (val > 0) {
setTimeout(() => {
this.count = this.count - 1
}, 1000)
}
}
},
methods: {
getVerifyCode () {
if (this.mobile === '') {
this.showToast('请输入签约手机号')
} else {

this.isGetVerifyCode = true
this.count = 60
}
},
againVerifyCode () {
if (this.count === '' || this.count === 0) {
this.count = 60







}
},
submit () {
if (this.accno === '') {
this.showToast('请输入签约账号')
return
}
if (this.certId === '') {
this.showToast('请输入证件号码')
return
}
if (!this.checked) {
this.showToast('请勾选签约协议')
return
}

this.$router.push('DetailsQuery')
},
clickCheck () {
this.checked = !this.checked
},
showAgreement () {
},
showToast (text) {
this.toastText = text
this.showPositionValue = true
},
getCertType (text) {
let certType = {
'身份证': '1'
}
return certType[text]
}
}
}



